<template>
    <div class="d-inline">

        <div class="tree-table-content ttc-ar"  :class="{ 'tree-root': the_item.index < 2 }">
            
            <div class="account_empty text-left tree-col" v-html="the_item.user.username">
                
            </div>
            
            <div class="ipv tree-col">
                {{ vip_code_to_name(the_item.vip) }}
            </div>
            <div class="usd_recommend tree-col">
                {{ the_item.usd_recommend }}
            </div>
            <div class="usd_active_house tree-col">
                {{ the_item.usd_active_house }}
            </div>
            <div class="usd_team_layer tree-col">
                {{ the_item.usd_team_layer }}
            </div>
            <div class="usd_leader_management tree-col">
                0
            </div>
            <div class="usd_leader_devlopment tree-col">
                0
            </div>
            <div class="usd_sale_commission tree-col">
                0
            </div>
            <div class="ipv tree-col">
                {{ total }}
            </div>
            <div class="option">
                <v-btn
                    class="ma-0 pa-1 btn-item-icon text-center"
                    fab
                    icon
                    x-small
                    color="primary"

                    @click="goCenter"
                >
                    <v-icon>
                        mdi-chevron-right
                    </v-icon>
                </v-btn>
            </div>
        </div>
    </div>
</template>

<script>

import vip_showing from '../../../../common/es/bonus/level/vip_showing'
import floatFuc from '../../../../common/es/bonus/tool/float'

    export default {
        components: {
            
        },
        props: {    
            the_item: {
                type: Object
            },
            allow: {
                type: Boolean
            }
        },
        name: '',
        mounted() {
            // console.log(this.the_item)
        },
        data() {
            return {
                
            }
        },
        computed: {
            total() {
                let res = floatFuc.floatAdd(
                    this.the_item.usd_recommend,
                    this.the_item.usd_active_house
                )
                return floatFuc.floatAdd(
                    res,
                    this.the_item.usd_team_layer
                )
            }
        },
        methods: {
            vip_code_to_name(rec) {
                
                if (rec) {
                    rec = vip_showing.split_code(rec)
                    return vip_showing.code_to_content(rec).name
                }

                return '無結果'
            },
            goCenter() {
                this.$store.commit('saveUser', this.the_item.user)
                this.$store.commit('saveBonus', this.the_item)

                this.$router.push({
                    path: '/home/account/statement',
                    query: {
                        user_id: this.the_item.user.id
                    }
                })
            }
        }
    }
</script>

<style lang="sass" scoped>
@import "@/common/sass/table_header.sass" 
    
</style>


