<template>
    
    <div class="username" :class="{ 'tree-root': the_item.index < 2, 'text-center': !need_tree }">

        <div :class="{ 'level-space' : need_tree }" v-for="i in the_item.index" :key="i"></div>
        <div class="inner-name d-inline">{{ the_item[space] }}</div>
    </div>

</template>

<script>
    export default {
        props: {
            the_item: {
                type: Object
            },
            space: {
                type: String,
                default: 'username'
            },
            need_tree: {
                type: Boolean,
                default: true
            }
        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
    .inner-name
        padding: 0px 12px
    .level-space
        width: calc( 12px + 1vw )
        height: 6px
        display: inline-block
</style>


