
import conf from '../conf'
const NAMESPACE = conf.NAMESPACE
const CAN_USER_PV_LIMIT = conf.CAN_USER_PV_LIMIT

const _build = function(ul, id, id_field, layer = -1) {
    const res = []

    layer += 1

    ul.map(e => {
        if (e[id_field] == id) {

            const unid = e[
                NAMESPACE.unid
            ]

            e[
                NAMESPACE.children
            ] = _build(ul, unid, id_field, layer)

            e[
                NAMESPACE.index
            ] = layer
            
            res.push(e)
        }
    });

    return res
}

const build_user_relation = function(ul, model, first_unid = undefined) {


    return new Promise((rej, rev) => {
        let users = null
    
        if (model == 'SPONSER') {
            users = _build(ul, first_unid, 
                NAMESPACE.sponser_id
            )
        } else {
            users = _build(ul, first_unid, 
                NAMESPACE.enroller_id
            )
        }

        rej(users)
    })
}

export default build_user_relation


