<template>

    <v-card>
        <v-row>
            <v-col cols="12">
                <v-btn color="primary ml-2 mb-3">
                    <sync-bonus :user_s="users_sponser" :user_e="users_enroller" @start_Father="async_start" @finished_Father="async_finished"></sync-bonus>   
                </v-btn>    
                <ar-tree-header></ar-tree-header>
            </v-col>

            <v-col cols="12" class="py-0">


                <v-treeview multiple-active transition dense off-icon
                    class="mt-0 the-tree ar-tree fs-14"

                    :items="bonuses" open-all :open="[0, 1]"
                    v-if="show"
                >
                    <template v-slot:label="{ item, open }">
                        <name-label :the_item="item.user" :op="open" :space="'number_code'" :need_tree="false"></name-label>
                    </template>
                    <template v-slot:append="{ item, open }">
                        <ar-tree-item ref="treeItem" :allow="can_num" :the_item="item" :o="open"></ar-tree-item>
                    </template>
                </v-treeview>

                <loading v-if="bonuses.length < 1"></loading>
            </v-col>
        </v-row>
    </v-card>

</template>

<script>

import rolling_api from '@/common/es/bonus/rolling_api'

import user_relation from '@/common/es/bonus/userApi/user_relation'
import ArTreeItem from './Inner/ArTreeItem.vue'
import NameLabel from '@/components/Tree/NameLabel.vue'
import ArTreeHeader from './Inner/ArTreeHeader.vue'

import SyncBonus from '@/components/Conning/SyncBonus.vue'
import Loading from '../../../components/Data/Loading.vue'
import LoadingHuge from '../../../components/Data/LoadingHuge.vue'

    export default {
        components: { ArTreeItem, NameLabel, ArTreeHeader, SyncBonus, Loading, LoadingHuge },
        name: '',
        data() {
            return {
                users_sponser: null,
                users_enroller: null,
                show: false,
                can_num: false,

                bonuses: [ ]
            }
        },
        mounted() {
            // this.user()
            this.theUser()

            this.bonusesLoading()
        },
        methods: {
            // 数据同步
            async theUser() {
                this.users_sponser = this.$store.state.users_sponser
                this.users_enroller = this.$store.state.users_enroller
                this.show = true
            },

            // 获取 奖金
            async bonusesLoading() {
                const res = await this.conn.get(
                    this.api.bonus,
                    this.$store.state.token,
                    {
                        'chronu': this.$store.state.chronus.id,
                        '_sort': 'published_at:ASC'
                    }
                )

                if (res) {
                    this.bonuses = [ ]
                    const transfer = this.$store.state.transfer
                    for (let i= 0; i< transfer.length; i++ ) {
                        const item = transfer[i]

                        for (let j= 0; j< res.length; j++ ) {

                            if (item.unid == res[j].user.unid) {
                                this.bonuses.push(res[j])
                            }
                        } 
                    }
                    
                    if (this.bonuses.length == 0) {
                        // alert('數據量 = 0。')
                    }
                }
            },


            // 获取 同步所需要的数据
            async user() {
                
                const s = await user_relation(this.$store.state.user_unit, 'SPONSER')

                if (s) {
                    this.users_sponser = JSON.parse(JSON.stringify(s))
                    rolling_api(this.users_sponser)
                }

                const e = await user_relation(this.$store.state.user_unit, 'ENROLLER')

                if (e) {
                    this.users_enroller = JSON.parse(JSON.stringify(e))
                    rolling_api(this.users_enroller)
                }

                this.show = true
            },

            async_start() {
                this.bonuses = [ ]
            },
            async_finished() {
                /*
                this.show = false
                setTimeout(() => {
                    this.show = true
                }, 300)
                */
               location.reload()
            }
        }
    }
</script>

<style lang="sass" scoped>
        
</style>


