import conf from '../conf'

const _simpler = conf.NAMESPACE.simpler
const _leader = conf.NAMESPACE.leader

const vip_0 = {
    name: '普通成员',
    type: _simpler,
    star: 0,
    ratio: [ 0 ]
}

const vips_simpler = conf.VIPS_SIMPLER
const vips_leader = conf.VIPS_LEADER

const _usd_ratio = function(type, star) {
    const i = star - 1
    return conf.ACTIVE_LAYER_RATIOS[type][i]

}

const get_vip_and_ratio = function(type, star, need_ratio = true) {

    let res = vip_0

    if (type == _leader) {
        vips_leader.map(e => {
            if (e.star == star) {
                res = e
            }
        })
    } else {
        vips_simpler.map(e => {
            if (e.star == star) {
                res = e
            }
        })
    }

    if (need_ratio && star > 0) {
        res.ratio = _usd_ratio(type, star)
    }
    return res
}

const _judge_leader = function(ipv, num, pv_son) {
    const pv = ipv + pv_son

    let star = 3
    if ((num > 4) && (pv > 25000) && (pv_son > 5000)) {

        if ((num >= 6) && (pv >= 150000) && (pv_son >= 25000)) {
            star = 5
        } else if ((num >= 5) && (pv >= 72000) && (pv_son >= 9000)) {
            star = 4
        }

    } else if ((num < 4) || (pv < 25000) || (pv_son < 5000)) {

        if ((num >= 3) && (pv >= 9000) && (pv_son >= 3000)) {
            star = 2
        } else {
            star = 1
        }
    }

    return star
} 

const _judge_simpler = function(ipv, num, pv_son) {
    const pv = ipv + pv_son

    let star = 3
    if (pv > 1000) {

        if (pv >= 3000) {
            star = 5
        } else if (pv >= 2000 ) {
            star = 4
        }

    } else if (pv < 1000) {

        if (pv < 500) {
            star = 1
        } else {
            star = 2
        }

    }
    return star
} 

const get_vip = function(ipv, num, pv_son) {

    let type = _simpler
    let star = 1

    if (ipv < 100) {
        star = 0
    } else {


        if ((pv_son > 2000) && (num >= 2) && (ipv + pv_son) >= 5000) {

            star = _judge_leader(ipv, num, pv_son)
            type = _leader
            
        } else {

            star = _judge_simpler(ipv, num, pv_son)
        }
    }

    return get_vip_and_ratio(type, star)
}


export default {
    get_vip,
}


