import conf from '../conf'

const _simpler = conf.NAMESPACE.simpler
const _leader = conf.NAMESPACE.leader

const vips_simpler = conf.VIPS_SIMPLER
const vips_leader = conf.VIPS_LEADER

// rec = list = 传入数组
const code_to_content = function(rec) {
    let res = ''
    if (rec[0] == _simpler) {
        if (rec[1] == 0) { return res }
        res = vips_simpler[ rec[1] - 1] // Number.parseInt(rec[1]
    } else {
        res = vips_leader[ rec[1] - 1]
    }

    return res
}

const split_code = function(rec, option = true) {

    let res = null

    if (option) {
        res = rec.split('_')
    } else {
        res = rec.join('_')
    }

    return res
}

export default {
    split_code,
    code_to_content
}


