<template>

    <div class="pl-4 pr-4 async-content-fixed sub-header" v-if="loading">
        
        <v-icon class="circle-around d-inline-block" size="14">mdi-loading</v-icon>
        <div class="fs-12 d-inline-block">同步中...</div>
    </div>

    <span v-else></span>

</template>

<script>
// 以 Boos 为单位的递归开头，每一个无上线成员均为一名 Boos
// 有多少 Boos 则有多少个递归运算

import LoadingSmall from '../Data/LoadingSmall.vue'
    export default {
        components: { LoadingSmall },
        props: {
            user_s: {
                type: Array
            },
            user_e: {
                type: Array
            }
        },
        name: '',
        data() {
            return {
                loading: false,
                _bonus: [ ],
                transfer: [ ]
            }
        },
        async mounted() {
            this.transfer = this.$store.state.transfer
            this._bonus = await this.getBonus()

            if (this._bonus.length > 0) {
                setTimeout(() => {
                    this.syncData()
                }, 4000);
            }
        },
        methods: {
            clear_data(data) {
                delete data.chronu
                delete data.createdAt,
                delete data.published_at,
                delete data.updatedAt,
                delete data.bonus
            },
            async getBonus() {
                let res = [ ]
                let b = await this.conn.get(
                    this.api.bonus, 
                    this.$store.state.token,
                    {
                        'chronu': this.$store.state.chronus.id
                    }
                )

                if (b) {
                    res = b.map(e => {
                        return {
                            id: e.id,
                            user: e.user.id
                        }
                    })
                }

                return res
            },

            async netoSave_enroller(user) {
                const data = { usd_recommend: user.bonus.usd_recommend }

                this._bonus.map(e => {
                    if (e.user == user.id) {
                        data.bonus = e.id
                    }
                })
                
                const res = await this.conn.put(
                    this.api.bonus + '/' + data.id,
                    this.$store.state.token,
                    data
                )

                if (res) { return true }
                return false
            },
            async netoSave_sponser(user) {
                const data = {
                    ...user.bonus, 
                    vip: user.vip.name + '_' + user.vip.star,
                    son_can_num_sponser: user.transfer.son_can_num_SPONSER,
                    son_can_num_enroller: user.transfer.son_can_num_ENROLLER,
                    son_pv_sponser: user.transfer.son_pv_SPONSER + '',
                    son_pv_enroller: user.transfer.son_pv_ENROLLER + ''
                }

                this._bonus.map(e => {
                    if (e.user == user.id) { data.bonus = e.id }
                })

                this.clear_data(data)
                
                const res = await this.conn.put(
                    this.api.bonus + '/' + data.id,
                    this.$store.state.token,
                    data
                )
                

                if (res) { return true }
                return false
            },
            syncData() {
                this.loading = true
                // this.$emit('start_Father')
                console.log(this.transfer)
                this.transfer.map(e => {
                    
                })
                // this.bonus._build(this.$store.state.users_sponser, this.transfer, this.netoSave_sponser)
                // this.bonus._build(this.$store.state.users_enroller, this.transfer, this.netoSave_enroller)

                setTimeout(() => {
                    this.loading = false
                    // this.$emit('finished_Father')
                }, 4600)
            }
        }
    }
</script>

<style lang="sass" scoped>
    
.async-content-fixed
    position: absolute
    bottom: 0
    z-index: 999

</style>


